<template>
  <div>
    <h1 style="text-align: center">Android</h1>
    <el-form ref="form" :model="form" label-width="100px" label-position="left">
      <el-form-item label="">
        <el-input v-model="form.appName" value="Android" label="Android"
          >Android</el-input
        >
      </el-form-item>
      <el-form-item label="git">
        <el-select v-model="form.branch" placeholder="">
          <el-option label="master" value="master"></el-option>
          <el-option label="dev" value="dev"></el-option>
        </el-select>
        <el-select v-model="form.base_branch" placeholder="">
          <el-option label="master" value="master"></el-option>
          <el-option label="dev" value="dev"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-col :span="11">
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.date1"
            style="width: 100%"
          ></el-date-picker>
        </el-col>
        <el-col class="line" :span="2" style="text-align: center">-</el-col>
        <el-col :span="11">
          <el-time-picker
            placeholder=""
            v-model="form.date2"
            style="width: 100%"
          ></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="form.incremental"></el-switch>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="form.condition">
          <el-checkbox label="" name="type"></el-checkbox>
          <el-checkbox label="" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="form.env">
          <el-radio label="Debug"></el-radio>
          <el-radio label="Release"></el-radio>
        </el-radio-group>
      </el-form-item>

      <div style="text-align: center">
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button></el-button>
        <el-button style="margin-top: 10px"></el-button>
      </div>

      <el-form-item label="" v-if="form.desc">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      form: {
        appName: "CC-Android",
        branch: "dev",
        base_branch: "master",
        date1: "",
        date2: "",
        incremental: false,
        condition: [""],
        env: 'Debug',
        desc: "",
      },
    };
  },
  methods: {
    onSubmit() {
      console.warn(this.form);
    },
  },
};
</script>


