<template>
  <div>
    <h1 style="text-align: center">ec</h1>
    <el-upload
      class="upload-demo"
      action="http://127.0.0.1:8090/WebServer/JacocoApi/uploadEcFile"
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :before-remove="beforeRemove"
      :data="otherData"
      multiple
      :limit="3"
      :on-exceed="handleExceed"
      :on-success="handleSucceed"
      :on-error="handleError"
      :file-list="fileList"
    >
      <el-button size="small" type="primary"
        >APP</el-button
      >
      <div slot="tip" class="el-upload__tip">
        /Sdcard/Android/packagename/Cache/jacoco/xxx.ec
      </div>
    </el-upload>
  </div>
</template>
<script>
export default {
  data() {
    return {
      otherData: {
        appName: "android",
        versionCode: "3.8.1",
      },
      fileList: [
        // {
        //   name: "food.jpeg",
        //   url: "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100",
        // },
      ],
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 3  ${files.length}  ${
          files.length + fileList.length
        } `
      );
    },
    handleSucceed() {
      this.$message.success(``);
    },
    handleError(err) {
      let { type } = err;
      //debugger
      //function(err, file, fileList)
      this.$message.error(`${type ? ":" + type : ""}`);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
  },
};
</script>


